import { GET_RAFFLE_LIST, UPDATE_JOIN_RAFFLE,JOIN_RAFFLE ,GET_RAFFLE_DETAILS,GET_COMPLETED_RAFFLE_LIST ,GET_TODAY_COMPLETED_RAFFLE_LIST ,UPDATE_RAFFLE_REQUIRED_MEMBERS, TOP_RAFFLES } from '../actions/types';

const initialState = {
    raffleList: {},
    completedRaffleList:{},
    completedRaffleListToday:{},
    raffle:{},
    loading:{},
    raffleDetails:{},
    joinRaffleData:{},
    topRaffles: {}
  };
   
  export default function(state = initialState, action) {
    switch (action.type) {
      case GET_RAFFLE_LIST:
        return {
          ...state,
          raffleList: action.payload
        };
      case GET_COMPLETED_RAFFLE_LIST:
        return {
          ...state,
          completedRaffleList: action.payload
        };
      case GET_TODAY_COMPLETED_RAFFLE_LIST:
        return {
          ...state,
          completedRaffleListToday: action.payload
        };
      case UPDATE_JOIN_RAFFLE:
        return {
          ...state,
          raffle: action.payload
        };
        case JOIN_RAFFLE:
        return {
          ...state,
          joinRaffleData: action.payload
        };
        case UPDATE_RAFFLE_REQUIRED_MEMBERS:
        return {
          ...state,
          requiredMembers: action.payload
        };
        case GET_RAFFLE_DETAILS:
        return {
          ...state,
          raffleDetails: action.payload
        };
        case TOP_RAFFLES:
        return {
          ...state,
          topRaffles: action.payload
        };
      default:
        return state; 
    }
  }