import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Home from "./components/home/main";
import Faq from "./components/home/faq";
import RaffleCreate from "./components/raffle/create";

import { Provider } from "react-redux";
import store from "./store";
import PrivateRoute from "./components/common/PrivateRoute";

import "./App.css";
import RaffleJoin from "./components/raffle/join";
import RaffleHistory from "./components/raffle/history";


class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
         
            <Route exact path="/" component={Home} />
            <Route exact path="/create" component={RaffleCreate} />
            <Route exact path="/join" component={RaffleJoin} />
            <Route exact path="/history" component={RaffleHistory} />
            <Route exact path="/faq" component={Faq} />
            
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
