import { USER_INFO, USER_LOADING } from '../actions/types';

const initialState = {
  userDetails: {},
  loading: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case USER_LOADING:
      return {
        ...state,
        loading:true
      };
    case USER_INFO:
      return {
        ...state,
        userDetails: action.payload,
        loading:false
      };
    default:
      return state;
  }
}
