import axios from 'axios';
// import jwt_decode from 'jwt-decode';
import { config } from '../utils/config';
import { GET_ERRORS, USER_INFO, SUCCESS_MSG, UPDATE_JOIN_RAFFLE,JOIN_RAFFLE, GET_RAFFLE_LIST, GET_COMPLETED_RAFFLE_LIST ,GET_TODAY_COMPLETED_RAFFLE_LIST ,GET_RAFFLE_DETAILS , UPDATE_RAFFLE_REQUIRED_MEMBERS, TOP_RAFFLES } from './types';


axios.defaults.withCredentials = true

export const fetchUserDetails = () => dispatch => {
  axios
    .get(`${config.apiUrl}/user/info`)
    .then(res => {
      console.log('444');
      console.log('response from user details --',   res)
      if(!res.data.status ){
        dispatch({ 
          type: GET_ERRORS,
          payload: res.data.msg
        });
      }else{
      dispatch({
        type: USER_INFO,
        payload: res.data
      });
    }
    })
    .catch((err) =>{
      console.log(err)
      dispatch({ 
        type: GET_ERRORS,
        payload: err
      });
    }
    );
  };


